<template>
  <div id="WebGL" style="width: 100%; height: 100%"></div>
</template>


<script>
import { onBeforeUnmount, onMounted } from "@vue/runtime-core";
import VueScene from "../script/View3D";
import { ElLoading } from "element-plus";
import circlefrag from "../shader/shaderCircle.frag";
import circlevert from "../shader/shaderCircle.vert";
export default {
  setup() {
    let shaderScene;
    let loadingInstance;

    onMounted(() => {
      loadingInstance = ElLoading.service({ fullscreen: true });
      shaderScene = new VueScene("WebGL");
      shaderScene.camera.position.set(100, 100, 100);
      shaderScene.axesHelper();

      shaderPlane().then(() => {
        loadingInstance.close();
      });
    });

    onBeforeUnmount(() => {
      shaderScene.SceneDestory();
      console.log("场景销毁");
    });

    const shaderPlane = async () => {
      const circleTexture = await shaderScene.loadTexture_Y(
        "./shaderResources/huan.png"
      );

      const geometry = shaderScene.createPlaneGeometry(100, 100, 99, 99);

      const material = shaderScene.setRawShaderMaterial({
        uniforms: {
          time: {
            value: 1.0,
          },
          textures: {
            value: circleTexture,
          },
        },
        vertexShader: circlevert,
        fragmentShader: circlefrag,
      });

      let data = 0;
      setInterval(() => {
        data += 0.1;
        material.uniforms.time.value = Math.sin(data) * 10;
      }, 50);

      const plane = shaderScene.createMesh(geometry, material);
      plane.rotation.x = -Math.PI / 2;

      shaderScene.scene.add(plane);
    };
  },
};
</script>